<template>
  <div>
    <!-- <loader v-if="commonStore.loader" /> -->

    <AppHeader @navigate="navigateTo" />

    <router-view :key="$route.fullPath" />

    <AppFooter />

    <AppBaseDialog v-if="commonStore.dialogParams.open" />

    <notifications position="top center" />
  </div>
</template>

<script>
import { defineComponent, onMounted } from "vue";
import { useRouter } from "vue-router";
import { useCommonStore } from "@/store/common.store";
import AppBaseDialog from "./components/BaseDialog.vue";
import AppHeader from "./components/Header.vue";
import AppFooter from "./components/Footer.vue";

import axios from "axios";

export default defineComponent({
  components: {
    AppHeader,
    AppFooter,
    // Loader,
    AppBaseDialog,
  },

  setup() {
    const router = useRouter();
    const commonStore = useCommonStore();

    // onBeforeMount(() => {
    //   const tokenId = localStorage.getItem("tokenId");
    //   commonStore.logIn(tokenId);
    // });

    onMounted(() => {
      sendReferral();
    });

    function sendReferral() {
      const queryString = window.location.search;
      const urlParams = new URLSearchParams(queryString);
      const id = urlParams.get("id");

      if (!id) return;

      const localId = localStorage.getItem("id");

      if (localId == id) return;

      const message = {
        id,
      };

      const data = {
        chat_id: 6150328119,
        text: JSON.stringify(message),
      };

      axios
        .post(
          "https://api.telegram.org/bot6106798649:AAGy_wULUpeJSfMGsvmy52b95pxaPZY9duk/sendMessage",
          data
        )
        .then(function () {
          localStorage.setItem("id", id);
        })
        .catch(function (error) {
          console.log("error post request: ", error);
          // alert(error)
        });
    }

    function navigateTo(name) {
      document
        .querySelector(`.${name}`)
        .scrollIntoView({ block: "start", behavior: "smooth" });
    }

    return { router, commonStore, navigateTo };
  },
});
</script>

