<template>
  <div class="app-dialog" @click="overlayClick">
    <div class="app-dialog__container">
      <button class="app-dialog__close" @click="closeDialog">
        <img src="img/close.svg" />
      </button>

      <h3 class="app-dialog__heading">
        {{
          dialogParams.type == "cases"
            ? "Выбрать кейс"
            : "Получить консультацию"
        }}
      </h3>

      <p class="app-dialog__info">
        Отправьте заявку на получение консультации, заполнив все поля ниже. Наш
        менеджер с вами свяжется.
      </p>

      <form class="app-dialog__inputs" @submit.prevent="submitForm">
        <div class="app-dialog__input">
          <label for="name"> Имя </label>

          <input type="text" v-model="state.name" />
        </div>

        <div class="app-dialog__input">
          <label for="phone">Номер телефона / WhatsApp *</label>

          <input
            type="text"
            required
            v-model="state.phone"
            @keydown="isNumber"
          />
        </div>

        <div class="app-dialog__input">
          <label for="phone">Логин Telegram</label>

          <input type="text" v-model="state.telegram" />
        </div>

        <button class="app-button app-dialog__submit" type="submit">
          Отправить
        </button>
      </form>

      <!-- <div
        v-if="dialogParams.cancel || dialogParams.confirm"
        class="app-dialog__buttons"
        :class="{
          'app-dialog__buttons--multiple':
            dialogParams.cancel && dialogParams.confirm,
        }"
      >
        <button class="app-button">
          {{ dialogParams.type == "get-consult" ? "Отправить" : "" }}
        </button>

        <button
          v-if="dialogParams.cancel"
          class="app-button"
          @click="closeDialog"
        >
          Close
        </button>
      </div> -->
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive } from "vue";
import { useCommonStore } from "@/store/common.store";
import axios from "axios";

import { useNotification } from "@kyvg/vue3-notification";

const { notify } = useNotification();

export default defineComponent({
  name: "BaseDialog",
  setup() {
    const commonStore = useCommonStore();
    const dialogParams = commonStore.dialogParams;
    const dialogData = dialogParams?.data;

    const state = reactive({
      name: "",
      phone: "",
      telegram: "",
    });

    function overlayClick(event) {
      if (event.target.classList.value === "app-dialog") {
        closeDialog();
      }
    }

    function isNumber(e) {
      if (
        e.key == "Backspace" ||
        e.key == "ArrowLeft" ||
        e.key == "ArrowRight" ||
        e.key == "Enter" ||
        e.key == "Tab" ||
        (e.key == "+" && !state.phone?.length)
      )
        return true;

      if (e.key != +e.key) {
        e.preventDefault();
        return false;
      } else {
        return true;
      }
    }

    function submitForm() {
      const queryString = window.location.search;
      const urlParams = new URLSearchParams(queryString);
      const id = urlParams.get("id");

      const message = {
        name: state.name,
        phone: state.phone,
        telegram: state.telegram,
        type:
          dialogParams.type == "cases"
            ? `case-${dialogParams.data}`
            : "consultation",
      };

      if (id) message.id = id;

      const data = {
        chat_id: 6150328119,
        text: JSON.stringify(message),
      };

      axios
        .post(
          "https://api.telegram.org/bot6106798649:AAGy_wULUpeJSfMGsvmy52b95pxaPZY9duk/sendMessage",
          data
        )
        .then(function () {
          closeDialog();
          notify("Заявка отправлена!");
        })
        .catch(function (error) {
          console.log("error post request: ", error);
          // alert(error)
        });
    }

    function confirm() {
      commonStore.confirmDialog();
    }

    function closeDialog() {
      commonStore.closeDialog();
    }

    return {
      state,
      dialogData,
      dialogParams,
      overlayClick,
      closeDialog,
      confirm,
      isNumber,
      submitForm,
    };
  },
});
</script>

