import { defineStore } from 'pinia'

export const useCommonStore = defineStore({
  id: 'Common',
  state: () => ({
    tokenId: null,
    userSettings: null,
    // loader: false,
    dialogParamsData: {
      open: false,
      confirm: false,
      type: "",
      data: {}
      // component: undefined,
      // maxWidth: 544,
    },
    sidebarOpen: true,
    sidebarOpenWidth: 256,
    sidebarClosedWidth: 64
  }),
  getters: {
    dialogParams: state => state.dialogParamsData,
    // isDesktop: state => state.windowWidth >= 768,
    // windowWidthLess1300: state => state.windowWidth <= 1300,
    // isMobile: state => state.windowWidth < 768,
  },
  actions: {
    initNewDialog(payload) {
      payload.open = true;
      payload.confirm = false;

      this.$patch({
        dialogParamsData: payload
      })
    },
    confirmDialog() {
      this.$patch({
        dialogParamsData: {
          confirm: true,
          open: false
        }
      })
    },
    closeDialog() {
      this.$patch({
        dialogParamsData: {
          open: false
        }
      })
    },
  }
})
